import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeAdminHeaderModule } from './home-admin-header/home-admin-header.module';
import { HomeAdminComponent } from './home.component';

const routes: Routes = [
{path: '', redirectTo: 'i', pathMatch: 'full'},
  {
    path: 'login',
    loadChildren: () =>
      import('./login/login.module').then((m) => m.LoginAdminModule),
  },
  {
    path: 'i',
    loadChildren: () =>
      import('./../main-nav/main-nav.module').then((m) => m.MainAdminModule),
  },
];
@NgModule({
  declarations: [HomeAdminComponent],
  imports: [CommonModule, RouterModule.forChild(routes), HomeAdminHeaderModule],
})
export class HomeAdminModule {}
